.head {
    height: 70px;
    background-color: #F6F6F6;
    opacity: 0.8;
    -moz-box-shadow: inset 0 0 10px #000000;
    -webkit-box-shadow: inset 0 0 10px #000000;
    box-shadow: 0px 15px 10px -15px #111;
}

.text {
    font-family: Bradley Hand, cursive;
    font-size: 20px;
    color: #282828;
    position: absolute;
    margin-top: -50px;
    margin-left: 100px;
}

.work {
    font-family: Georgia, serif;
    font-size: 15px;
    position: absolute;
    margin-top: -45px;
    margin-left: 82em;
}

.about {
    font-family: Georgia, serif;
    font-size: 15px;
    position: absolute;
    margin-top: -45px;
    margin-left: 85em;
}

.rintro {
    height: 600px;
    position: relative;
    background-color: #E5E5E5;
}

.rintro .rposter {
    height: 600px;
    width: 800px;
    background: url(../resources/rposter.png);
    overflow: hidden;
    background-size: cover;
    display: inline-block;
    position: absolute;
    opacity: 0.7;
    margin-top: -1%;
    margin-left: 5%;
}

.rintro .caption {
    font-family: Impact, Times, serif;
    color: red;
    font-size: 40px;
    font-weight: 500;
    position: absolute;
    margin-left: 46%;
    margin-top: 16%;
}

.rintro .details {
    font-family: Impact, Times, serif;
    color: black;
    font-size: 20px;
    font-weight: 500;
    position: absolute;
    margin-left: 46%;
    margin-top: 20%;
}


.project {
    background-color: black;
    height: 500px;
    /*position: relative;*/
}

.project .poster {
    height: 500px;
    width: 600px;
    background: url(../resources/gamestop.png);
    overflow: hidden;
    background-size:cover;
    display: inline-block;
}

.project .brief {
    display: inline-block;
    margin-left: 10%;
    margin-top: 7%;
    position: absolute;
}

.project .timeline {
    display: inline-block;
    margin-left: 10%;
    margin-top: 17%;
    position: absolute;
}

.project .role {
    display: inline-block;
    margin-left: 10%;
    margin-top: 23%;
    position: absolute;
}

.project .caption {
    color: #E5E5E5;
    font-family: Impact, Times, serif;
    font-size: 25px;
    font-weight: 500;
}

.project .details {
    width: 15cm;
    margin-top: -5px;
    color: #E5E5E5;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 16px;
    font-weight: 300;
}

